<template>
  <div class="date-pick">
    <!-- 历史查询时间 -->
    <div class="history-search-wp">
      <div class="left-arrow-wp"  @click="prevTime">
        <img src="../../assets/lishi-left.png" alt="" class="left-arrow">
      </div>
      <span class="time-from" @click="pickFromTime">{{fromTime | dashDate}}</span>
      <span class="text">至</span>
      <span class="time-end" @click="pickEndTime">{{endTime | dashDate}}</span>
      <div class="right-arrow-wp" @click="nextTime">
        <img src="../../assets/lishi-right.png" alt="" class="right-arrow">
      </div>
    </div>
    <!-- 时间选择器 -->
    <van-popup v-model="timePickerShow" position="bottom">
      <van-datetime-picker v-model="timePicker.currentDate" type="date" :min-date="MIN_DATE" :max-date="MAX_DATE" @confirm="pickTimeOk" @cancel="pickTimeCancel"/>
    </van-popup>
  </div>
</template>

<script>
// filters
import {dashDate} from '@/filters/index';
// util
import _util from '@/util/index';

export default {
  data() {
    return {
      fromTime: new Date(),
      endTime: new Date(),
      timePickerShow: false,

      timePicker: {
        // from, end
        type: 'from',
        currentDate: new Date(),
      },
    }
  },
  computed: {
    timepickedObj() {
      return {
        beginTime: _util.dashDateWithoutSpace(this.fromTime),
        endTime: _util.dashDateWithoutSpace(this.endTime),
      }
    }
  },
  props: {
    MIN_DATE: {
      default() {
        return new Date(2018, 0, 1);
      }
    },
    MAX_DATE: {
      default() {
        return new Date();
      }
    }
  },
  methods: {
    prevTime() {
      this.fromTime = this.endTime = new Date(this.fromTime.getTime() - 86400 * 1000);
      this.$emit('datepickChange', this.timepickedObj)
    },
    nextTime() {
      this.fromTime = this.endTime = new Date(this.endTime.getTime() + 86400 * 1000);
      this.$emit('datepickChange', this.timepickedObj)
    },
    pickTimeOk(value) {
      if(this.timePicker.type === 'from') {
        this.fromTime = value;
      }
      else if(this.timePicker.type === 'end'){
        this.endTime = value;
      }
      this.timePickerShow = false;
      if(this.fromTime.getTime() > this.endTime.getTime()) {
        this.$toast('开始时间大于结束时间');
        return;        
      } else {
        this.$emit('datepickChange', this.timepickedObj)
      }
    },
    // 取消选取时间
    pickTimeCancel() {
      this.timePickerShow = false;
    },
    // 选取开始时间初始化
    pickFromTime() {
      this.timePicker.currentDate = this.fromTime;
      this.timePicker.type = 'from';
      this.timePickerShow = true;
    },
    // 选取结束时间初始化
    pickEndTime() {
      this.timePicker.currentDate = this.endTime;
      this.timePicker.type = 'end';
      this.timePickerShow = true;
    },
  },
  filters: {
    dashDate
  }
}
</script>

<style lang="scss">
.date-pick{
  .history-search-wp{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f3f3f3;
    height: 45px;
    font-size: 15px;
    color: #1bbc9b;
    .left-arrow-wp,
    .right-arrow-wp{
      display: flex;
      align-items: center;
      position: absolute;
      padding: 0 10px;
      height: 45px;
    }
    .left-arrow-wp{
      left: 15px;
    }
    .right-arrow-wp{
      right: 15px;
    }
    .left-arrow,
    .right-arrow{
      width: 9.5px;
      height: 17px;
    }
    .text{
      padding: 0 30px;
      color: #666;
    }
  }
}
</style>




