<template>
  <div id="eight">
    <div class="nav-wp">
      <van-nav-bar title="指标分析" @click-left="goBack" class="nav-bar">
        <div class="goback-btn-wp" slot="left">
          <img src="./zong-fanhui.png" alt="" class="goback-btn">
        </div>
      </van-nav-bar>
      <!-- sub navbar -- filter -->
      <van-tabbar v-model="subTabbarActive" class="sub-tabbar">
        <van-tabbar-item v-for="(item, index) in subbarList" :key="index" @click="filterDropdownClick(item, index)">
          <span>{{item.activeItemValue.label || item.label}}</span>
          <div slot="icon" slot-scope="props" class="sub-tabbar-icon-wp">
            <img src="./xiala02.png" alt="" v-if="props.active">
            <img src="./xiala01.png" alt="" v-else>
          </div>
        </van-tabbar-item>
      </van-tabbar>
    </div>
    <datepick-from-to @datepickChange="datepickChange"></datepick-from-to>
    <div class="main-wp" :class="'table-class-' + activeIndex1">
      <div class="fix">
        <div class="equiment-wp">
          <div class="name">
            <span class="diamond"></span>
            <span class="title">{{subbarList[1].activeItemValue.label + '：'}}</span>
            <span class="value">100%</span>
          </div>
        </div>
        <div class="title-wp">
          <span class="title-item" v-for="(item, index) in tableTitleList[activeIndex1].labelList" :key="index">{{item.label}}</span>
        </div>
      </div>
      <no-result v-if="noResult"></no-result>
      <div class="sticky-wp" v-else>
        <div class="table-main">
          <div v-for="(item, index) in tableList" :key="index" class="table-item border-1px">
            <!-- <router-link tag="div" class="item-wp"> -->
              <span class="item" v-for="(item2, index) in item.tableShowList" :key="JSON.stringify(item2) + index">{{item2 || ' '}}</span>
            <!-- </router-link> -->
          </div>
        </div>
        <van-pagination class="pagination" v-model="pageNo" force-ellipses :total-items="totalLen" :items-per-page="pageSize" v-if="totalLen" :show-page-size="2" @change="pageNoChange" />
      </div>
    </div>
    <!-- 条件筛选 popup -->
    <van-popup v-model="filterPopupShow" class="filter-popup" overlay-class="filter-overlay">
      <span class="item" v-for="(item, index) in filterList" :key="index" @click="filterColumnItemClick(item, index)" :class="{active: subTabItemActive === index}">{{item.label}}</span>
    </van-popup>
  </div>
</template>

<script>
// 组件
import NoResult from '@/components/no-result/index';
// import SearchInput from '@/components/search-input/index'
import DatepickFromTo from '@/components/datepick-from-to/index';
// mixin
import apiMixin from './mixins/api-mixin';
import commonApiMixin from '@/mixins/common-api-mixin';
// util
import _util from '@/util/index';
// const
// 指标类型 / table title
import {indexTypeList, tableTitleList} from '@/common/const/zbfx';
// req const
import {loginOrgId, city, qg} from '@/common/const/zbfx';

const subbarList = [
  {label: '区域', type: 0, activeItemIndex: 0, activeItemValue: ''},
  {label: '指标类型', type: 1, activeItemIndex: 0, activeItemValue: ''},
];

const filterActiveList = [
  {
    index: -1,
    indexValue: '',
  },
  {
    index: 0,
    indexValue: '',
  },
];

export default {
  mixins: [commonApiMixin, apiMixin],
  data() {
    return {
      indexTypeList,
      tableTitleList,

      subTabbarActive: 0,
      subTabItemActive: '',

      subbarList,
      filterActiveList,

      filterPopupShow: false,
      filterList: [],

      areaList: '',
      // 所有feeder
      feederList: '',

      // feeder search
      feederListSearched: '',
      feederListSearchValue: '',
      
      // req;
      areaId: -1,
      indexType: 1,
      // 起止时间
      beginTime: _util.dashDateWithoutSpace(new Date()),
      endTime: _util.dashDateWithoutSpace(new Date()),
      pageNo: 1,
      pageSize: 10,

      // 分页
      totalLen: 0,

      tableList: [],


      showPlaceholder: true,
    }
  },
  computed: {
    noResult() {
      return this.tableList.length ? false : true;
    },
    // 指标分析activeIndex
    activeIndex1() {
      return this.subbarList[1].activeItemIndex;
    },
    class() {
      return 'table-class-' + this.activeIndex1;
    }
  },
  methods: {
    /**
     * component DatepickFromTo emit事件 
     * 时间选择发生变化；
    */
    datepickChange(timeObj) {
      this.beginTime = timeObj.beginTime;
      this.endTime = timeObj.endTime;
      this.refreshTableList();
    },
    goBack(){
      // this.$router.replace({path: '/'});
      // android go to index.html;
      webCtrl.finish()
    },
    // subbar点击
    filterDropdownClick(item, index) {
      // 点击区域
      if(item.type === 0) {
        if(this.areaList.length) {
          this.filterList = this.areaList;
        }
        else {
          this.$toast('无区域列表');
          return;
        }
      }
      // 点击指标类型
      else if(item.type === 1) {
        this.filterList = indexTypeList;
      }

      this.subTabItemActive = this.subbarList[item.type].activeItemIndex;
      // this.subTabItemActive = this.filterActiveList[item.type].index;
      this.filterPopupShow = true;
    },
    // subbar item点击
    filterColumnItemClick(item, index) {
      // 当前点击项属于哪个subbar Index
      // this.subTabbarActive
      let subbarItem = this.subbarList[this.subTabbarActive];
      // 保存点击的item，供其他tabbar切换时使用；
      this.subbarList[subbarItem.type].activeItemIndex = index;
      this.subbarList[subbarItem.type].activeItemValue = item;
      // 点击某个区域
      if(this.subTabbarActive === 0 ) {
        this.areaId = item.id;
        this.tableList = [];

        this.refreshTableList();
      }
      // 点击指标类型
      else if(this.subTabbarActive === 1) {
        this.indexType = item.id;
        this.pageNo = 1;
        this.refreshTableList();
      }
      this.filterPopupShow = false;
    },
    // 切换分页
    pageNoChange(value) {
      this.pageNo = value;
      // 终端在线率
      this.refreshTableList();
    },
    refreshTableList() {
      this.tableList = [];
      switch(this.activeIndex1) {
        case 0:
          this.getTermOnlineInfo();
          break;
        case 1:
          this.getYkdzUse();
          break;
        case 2:
          this.getYxdzCorrect();
          break;
        case 3: 
          this.getYkdzSuccess();
          break;
        case 4:
          this.getFASuccessMX();
          break;
        case 5:
          this.getFATYXLMX();
          break;
        default:
          break;
      }
    }
  },
  components: {
    NoResult,
    DatepickFromTo,
  },
  created() {
    this.getAreaList().then(() => {
      if(!this.areaList.length) return;
      this.subbarList[0].activeItemValue = this.areaList[0];
    });

    this.getTermOnlineInfo();

    this.subbarList[1].activeItemValue = this.indexTypeList[0];
  },
}
</script>

<style lang="scss">
@import "@/common/scss/index.scss";

#eight{
  height: 100%;
  .nav-wp{
    height: 90px;
  }
  @extend %nav-with-one-title;
  @extend %sub-tabbar;
  @include subbar-popup(90px);
  .name{
    display: flex;
    align-items: center;
    // padding: 5px 0 5px 10px;
    text-align: left;
    height: 45px;
    line-height: 45px;
    font-size: 15px;
    justify-content: center;
    // .diamond{
    //   margin-top: -3px;
    //   margin-right: 10px;
    //   width: 9px;
    //   height: 9px;
    //   background: #1bbc9b;
    // }
  }
  .main-wp{
    position: relative;
    height: calc(100% - 135px);
    // 表头
    .title-wp{
      // display: flex;
      // justify-content: space-around;
      padding: 9.5px 0;
      line-height: 1.5;
      height: 40px;
      // line-height: 40px;
      font-size: 0;
      text-align: center;
      background-color: #33d8b6;
      color: #fff;
      .title-item{
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        // flex-basis: 20%;
      }
    }
    .sticky-wp{
      height: calc(100% - 85px);
      overflow: auto;
      background-color: #f3f3f3;
      .table-main{
        min-height: 100%;
        padding-bottom: 40px;
        .table-item{
          padding: 15px 0;
          line-height: 1.5;
          font-size: 0;
          background-color: #fff;
          @include border-1px(#dcdcdc);
          .item{
            display: inline-block;
            padding: 0 5px;
            width: 20%;
            word-break: break-word;
            vertical-align: middle;
            font-size: 13px;
            text-align: center;
          }
        }
      }
    }
    .pagination{
      margin-top: -40px;
    }
  }
  // 终端在线率
  .table-class-0{
    .title-wp{
      .title-item{
        width: 20%;
      }
    }
    .table-main{
      
    }
  }
  // 遥控使用率
  .table-class-1{
    %placeholder {
      &:nth-child(1) {
        width: 25%;
      }
      &:nth-child(2) {
        width: 20%;
      }
      &:nth-child(3) {
        width: 55%;
      }
    }
    .title-wp{
      .title-item{
        @extend %placeholder;
      }
    }
    .table-main{
      .table-item{
        .item{
          @extend %placeholder;
        }
      }
    }
  }

  // 遥信正确率
  .table-class-2{
    %placeholder {
      &:nth-child(1) {
        width: 25%;
      }
      &:nth-child(2) {
        width: 25%;
      }
      &:nth-child(3) {
        width: 50%;
      }
    }
    .title-wp{
      .title-item{
        @extend %placeholder;
      }
    }
    .table-main{
      .table-item{
        .item{
          @extend %placeholder;
        }
      }
    }
  }
  // 遥控成功率
  .table-class-3{
    %placeholder {
      &:nth-child(1) {
        width: 25%;
      }
      &:nth-child(2) {
        width: 20%;
      }
      &:nth-child(3) {
        word-break: break-all;
        width: 55%;
      }
    }
    .title-wp{
      .title-item{
        @extend %placeholder;
      }
    }
    .table-main{
      .table-item{
        .item{
          @extend %placeholder;
        }
      }
    }
  }
  // FA成功率
  .table-class-4{
    %placeholder {
      &:nth-child(1) {
        width: 35%;
      }
      &:nth-child(2) {
        width: 25%;
      }
      &:nth-child(3) {
        word-break: break-all;
        width: 40%;
      }
    }
    .title-wp{
      .title-item{
        @extend %placeholder;
      }
    }
    .table-main{
      .table-item{
        .item{
          @extend %placeholder;
        }
      }
    }
  }

  // FA覆盖率
  .table-class-5{
    %placeholder {
      &:nth-child(1) {
        width: 30%;
      }
      &:nth-child(2) {
        width: 30%;
      }
      &:nth-child(3) {
        width: 20%;
      }
      &:nth-child(4) {
        width: 20%;
      }
    }
    .title-wp{
      .title-item{
        @extend %placeholder;
      }
    }
    .table-main{
      .table-item{
        .item{
          @extend %placeholder;
        }
      }
    }
  }

  .van-cell{
    padding: 0;
    height: 34px;
    line-height: 34px;
    .van-hairline--top-bottom::after{
      content: none;
      border: 0;
      height: 0;
    }
    .van-cell__value::after{
      height: 0;
      content: none;
      border: 0;
    }
    .van-field__control {
      font-size: 14px;
      text-align: center;
      &:focus {
        color: #666;
      }
    }
    input:-ms-input-placeholder{
      color: #c7c7c7;
    }
    input::-webkit-input-placeholder{
      color: #c7c7c7;
    }
  }
  .van-cell-group::after{
    content: none;
  }
  @extend %pagination;
}
</style>


