import * as types from '../mutation-types';

export default {
  state: {
    faultItem: {},
  },
  mutations: {
    [types.SET_FAULT_ITEM](state, obj) {
      state.faultItem = Object.assign({}, state.faultItem, obj);
    }
  },
};



